import zySearch from '../../components/zy-search/zy-search.vue'
export default {
	components: {
		zySearch
	},
	data() {
		return {
			searchText: '',
			historyList: uni.getStorageSync('search_cache'),
			wantList: ['美食', '住宿', '休闲', '娱乐', '美食', '住宿', '休闲', ]
		}
	},
	computed: {
		// historyList: {
		// 	get() {
		// 		return;
		// 	}
		// }
	},
	methods: {
		searchStart() {
			let _this = this;
			if (this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else {
				// this.hList.push(_this.searchText);
				// uni.setStorage({
				// 	key: 'search_cache',
				// 	data: _this.hList
				// });
				uni.getStorage({
					key: 'search_cache',
					success(res) {
						let list = res.data;
						console.log(list);
						if (list.length > 10) {
							for (let item of list) {
								if (item == _this.searchText) {
									return false;
								}
							}
							list.pop();
							list.unshift(_this.searchText);
						} else {
							for (let item of list) {
								if (item == _this.searchText) {
									return false;
								}
							}
							list.unshift(_this.searchText);
						}
						_this.historyList = list;
						uni.setStorage({
							key: 'search_cache',
							data: _this.historyList
						});
					},
					fail() {
						_this.historyList = [];
						_this.historyList.push(_this.searchText);
						uni.setStorage({
							key: 'search_cache',
							data: _this.historyList
						});
					}
				})
			}
		},
	},
}
